import { Button, LinearProgress, makeStyles, Paper, Slide, TextField, Typography } from "@material-ui/core";
import axios from "axios";
import React from "react";
import { connect } from "react-redux";
import { hwbountyAPI } from "../redux/types";
import t from '../util/localization/localization';
import TetLib from "../util/TetLib";
const { useState } = React;
const useStyles = makeStyles(theme => ({
  mainCard: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "flex-start",
    width: "100%",
    flexGrow: 1,
    backgroundColor:
      theme => theme === 1 ? "rgb(49,49,51)"
        :
        "rgb(255,255,255)",
    boxShadow: (theme) =>
      theme === 1
        ? '0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)!important'
        : '0 3px 6px rgba(0,0,0,0.1), 0 3px 6px rgba(0,0,0,0.01)!important',
    zIndex: 2,
    borderRadius: "1.5rem 1.5rem 0 0",
    minHeight: "70vh"
  },
  createYourAccountText: {
    fontSize: "1.5rem",
    marginTop: "3.5rem",
  },
  createYourAccountSubtitle: {
    fontSize: ".75rem",

  },
  secondaryOptionButton: {
    'text-transform': 'none!important',
    fontFamily: 'Poppins',
    fontSize: '.8rem',
    fontWeight: '400',
    color: '#69B4AC',
  },
  primaryOptionButton: {
    'text-transform': 'none!important',
    fontFamily: 'Poppins',
    fontSize: '1.3rem',
    fontWeight: '400',
    color: 'rgb(255,255,255)!important',
    backgroundColor: '#69B4AC',

    '&:hover': {
      backgroundColor: '#8CE0D7',
    },
  },
  createYourAccountButton: {
    paddingLeft: '2rem',
    paddingRight: '2rem',
    borderRadius: "0.5rem"
  }
}));
export const SignupMobile = props => {
  const {
    UI: { theme },
  } = props;
  const classes = useStyles(theme);

  const [success, setSuccess] = useState(false);
  const [errors, setErrors] = useState({
    firstNameError: '',
    lastNameError: '',
    emailError: '',
    usernameError: '',
    passwordError: '',
    passwordConfirmError: '',
  });
  let debounce = false;
  const onSubmit = () => {
    if (posting || debounce) return;
    setPosting(true);
    (async () => {
      const [firstName, lastName, email, username, password] =
        [
          document.getElementById('firstName').value,
          document.getElementById('lastName').value,
          document.getElementById('email').value,
          document.getElementById('username').value,
          document.getElementById('password').value,
        ];

      setErrors({
        firstNameError:
          errors.firstNameError || (!firstName && 'First Name required'),
        lastNameError:
          errors.lastNameError || (!lastName && 'Last Name required'),
        emailError:
          errors.emailError || (!email && 'Email required'),
        usernameError:
          errors.usernameError || (!username && 'Username required'),
        passwordError:
          errors.passwordError || (!password && 'Password required'),
      });
      if (Object.values(errors).filter((x) => x).length)
        return setPosting(false);
      const data = {
        firstName,
        lastName,
        email,
        username,
        password,
      };
      localStorage.setItem('signupStep1Data', JSON.stringify(data));
      let nameTaken = (
        await axios.get(`${hwbountyAPI}/usernameTaken/${username}`)
      )?.data;
      let errorObject = errors;
      if (nameTaken) {
        errorObject = Object.assign({}, errorObject, {
          usernameError: [
            null,
            'Invalid Username!',
            'Username has been taken!',
          ][nameTaken],
        });
      }
      if (!email.match(/\S+@\S+\.\S+/)) {
        errorObject = Object.assign({}, errorObject, {
          emailError: 'Invalid Email!',
        });
      }

      if (
        (
          await axios
            .get(`${hwbountyAPI}/emailTaken/${email}`)
            .catch(console.trace)
        )?.data
      ) {
        errorObject = Object.assign({}, errorObject, {
          emailError: 'Email already in use!',
        });
      }
      setPosting(false);
      setErrors(errorObject);
      if (Object.values(errors).filter((x) => x).length) return false;
      if ((await axios.post(`${hwbountyAPI}/signup`, data)).data.complete) {
        setPosting(false);
      }
    })();
  };
  const [firstName, setFirstName] = useState(
    JSON.parse(localStorage.getItem('signupStep1Data'))?.firstName || ''
  );
  const [lastName, setLastName] = useState(
    JSON.parse(localStorage.getItem('signupStep1Data'))?.lastName || ''
  );
  const [email, setEmail] = useState(
    JSON.parse(localStorage.getItem('signupStep1Data'))?.email || ''
  );
  const [username, setUsername] = useState(
    JSON.parse(localStorage.getItem('signupStep1Data'))?.username || ''
  );
  const [password, setPassword] = useState(
    JSON.parse(localStorage.getItem('signupStep1Data'))?.password || ''
  );
  const [passwordConfirm, setPasswordConfirm] = useState(
    JSON.parse(localStorage.getItem('signupStep1Data'))?.passwordConfirm || ''
  );
  const [posting, setPosting] = useState(false);
  const onChange = (errorName) => {
    ({
      firstName: setFirstName,
      lastName: setLastName,
      email: setEmail,
      username: setUsername,
      password: setPassword,
      passwordConfirm: setPasswordConfirm,
    }[errorName.substring(0, errorName.length - 5)](
      document.getElementById(errorName.substring(0, errorName.length - 5))
        .value
    ));
    setErrors(
      Object.assign({}, errors, {
        [errorName]: '',
      })
    );
  };

  return success ? (
    <div style={{ display: "flex", flexDirection: "column", flexWrap: "nowrap", height: "100%" }}>
      <div style={{ position: "sticky", top: 0, left: 0, zIndex: 1000 }}>
        {posting && <LinearProgress />}
      </div>

      <Typography variant="h6" style={{ textAlign: "left", margin: "24px", color: "rgb(106, 188, 171)" }}>HWBounty</Typography>
      <div style={{ position: "relative" }}>
        <img src="https://raw.githubusercontent.com/HWBounty/HWBountyAssets/a96175314671f3e367804f8b9f96f7f0cc227c40/Hopper_Hi_mobile.svg" style={{ marginBottom: "-30vw", zIndex: "0" }} />
        <p style={{ position: "absolute", top: "9.2%", left: "55.5%", color: "rgb(0,0,0)", fontSize: "3vw" }}>Hi! My name is Hopper!</p>
      </div>

      <Paper className={classes.mainCard}>
        <Typography className={`${classes.createYourAccountText}`}> Signup Successfull! Please be on the lookout for an email from Us!.</Typography>
        <br />

        <br />
        <br />
        <br />
      </Paper>
    </div >
  ) : (
    <div style={{ display: "flex", flexDirection: "column", flexWrap: "nowrap", height: "100%" }}
      onKeyDown={(key) =>
        key.code === 'Enter' && document.getElementById('loginButton').click()
      }
    >
      <div style={{ position: "sticky", top: 0, left: 0, zIndex: 1000 }}>
        {posting && <LinearProgress />}
      </div>

      <Typography variant="h6" style={{ textAlign: "left", margin: "24px", color: "rgb(106, 188, 171)" }}>HWBounty</Typography>
      <div style={{ position: "relative", backgroundAttachment: "scroll" }}>
        <img src="https://raw.githubusercontent.com/HWBounty/HWBountyAssets/a96175314671f3e367804f8b9f96f7f0cc227c40/Hopper_Hi_mobile.svg" style={{ marginBottom: "-30vw", zIndex: "0" }} />
        <p style={{ position: "absolute", top: "9.2%", left: "55.5%", color: "rgb(0,0,0)", fontSize: "3vw" }}>Hi! My name is Hopper!</p>
      </div>
      <Slide
        in
        direction="up"
        style={{
          transitionDelay: "250ms"
        }}
      >
        <Paper className={classes.mainCard}>
          <Typography className={`${classes.createYourAccountText}`}>Create Your Account!</Typography>
          <Typography className={`${classes.createYourAccountSubtitle}`}>(and say hi to Hopper)</Typography>
          <br />
          <form style={{ width: "90%" }}>
            <TextField variant="outlined" label="First Name" id="firstName" fullWidth style={{ margin: "0.5rem 0" }}
              required
              error={!!errors.firstNameError}
              helperText={errors.firstNameError}
              onChange={(e) => onChange('firstNameError')}
              value={firstName}
            />

            <TextField variant="outlined" label="Last Name" id="lastName" fullWidth style={{ margin: "0.5rem 0" }}
              required
              error={!!errors.lastNameError}
              helperText={errors.lastNameError}
              onChange={(e) => onChange('lastNameError')}
              value={lastName}

            />

            <TextField variant="outlined" label="Email" id="email" fullWidth style={{ margin: "0.5rem 0" }}
              required
              error={!!errors.emailError}
              helperText={errors.emailError}
              onChange={(e) => onChange('emailError')}
              value={email}
            />
            <TextField variant="outlined" label="Username" id="username" fullWidth style={{ margin: "0.5rem 0" }}
              required
              error={!!errors.usernameError}
              helperText={errors.usernameError}
              onChange={(e) => onChange('usernameError')}
              value={username}

            />
            <TextField variant="outlined" label="Password" id="password" fullWidth style={{ margin: "0.5rem 0" }}
              required
              error={!!errors.passwordError}
              helperText={errors.passwordError}
              onChange={(e) => onChange('passwordError')}
              value={password}
              type="password"
            />
          </form>
          <div style={{
            display: "flex",
            justifyContent: "space-between",
            width: "90%",
          }}>

            <Button
              className={`${classes.secondaryOptionButton}`}
              onClick={async () => {
                document.getElementById("hamburger").click();
                await TetLib.sleep(200);
                document.getElementById("clickLogin").click();

              }
              }
            >
              {t('newSignup.signInInstead')}
            </Button>

            <Button variant="contained" color="primary" className={`${classes.createYourAccountButton}`} disabled={Object.values(errors).filter((x) => x).length} onClick={onSubmit} id="loginButton">{t('newSignup.go')}</Button>
          </div>
          <br />
          <br />
          <br />
        </Paper>
      </ Slide >
    </div>

  );

};

const mapStateToProps = state => ({
  UI: state.UI,
  user: state.user,
});

export default connect(mapStateToProps)(SignupMobile);